<template>
  <div id="login" class="login">
    <div class="title" ref="title" :style="{backgroundPosition: bgPos}">
      <div style="height: 20%"></div>
      <span class="f1">Taptap</span>
      <br>
      <span class="f2">大数据分析展示平台</span>
      <div style="height:20%"></div>
      <div id="loginBox">
        <form @submit.prevent="Submit">
          <input type="text" placeholder="Username" id="name" v-model="user.name" class="f3"/>
          <input type="password" placeholder="Password" id="passwd" v-model="user.passwd" class="f3"/>
          <input type="submit" value="Sign in" class="f3" id="btn"/>
        </form>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Login',
  components: {
  },
  methods: {
    Submit(e){
      e.preventDefault();
      let formData = this.$qs.stringify(this.user)
      this.$axios.post('/login-check', formData).then(response => {
            // success callback
            let res = response.data
            if(res.flag === true){
              this.$router.push('/display')
              console.log(this.$route)
            }else {
              console.log(res)
            }
        }).catch(err => {
            // error callback
            console.log(err)
        });
    },
  },
  data(){
    return {
      user: {
        name: null,
        passwd: null
      },
      bgPos: '' 
    }
  },
  mounted(){
    document.addEventListener('scroll', ()=>{
      const y = window.scrollY;
      if(y != 0){
        this.bgPos = `${y}px ${y}px`
      }else {
        this.bgPos = '0 0'
      }
    })
  }

}
</script>

<style scoped>
    *{
      margin: 0px;
      padding: 0px;
    }
    .login {
      height: 200vh;
      background-image: url("../assets/loginBg.jpg");
      background-size: cover;
      position: relative;
    }
    .title{
      background-image: url("../assets/loginBg.jpg");
      background-size: cover;
      height: 200vh;
      text-align: center;
      -webkit-background-clip: text;
      background-clip: text;
      color: transparent;
      overflow: hidden;
    }
    .f1{
      font-size: 15vw;
      font-weight: 900;
    }
    .f2{
      font-size: 4vw;
      font-weight: 400;
    }
    .f3{
      font-size: 1vw;
      font-weight: 200;
      color: white;
    }
    #loginBox{
      width: 100%;
      height: 15%;
      display: flex;
      justify-content: center;
    }
    form{
      width: 25%;
      height: 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      align-content: space-around;
    }
    input{
      outline-style: none;
      border: white 1px solid;
      border-radius: 3%;
      width: 100%;
      padding: 3%;
      background-color: rgba(100,149,237,0.6);
    }
    input:focus{
      border-color: #66afe9;
      outline: 1px;
      -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6);
      box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6);
    }
    input:-moz-placeholder{
      color: white;
    }
    input:-ms-input-placeholder{
      color: white;
    }
    input::placeholder{
      color: white;
    }
    input::-webkit-input-placeholder{
      color: white;
    }
    #btn{
      background-color: transparent;
      color: rgb(3, 140, 252);
      border-color: rgb(3, 140, 252);
    }
    #btn:hover{
      border-color: white;
      outline: 1px;
      -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6);
      box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6);
      background-color: rgba(102,175,233,0.6);
      color: white;
    }
    
</style>
