import VueRouter from "vue-router";
import Login from '../pages/Login'
import Display from '../pages/Display'
export default new VueRouter({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'index',
            component: Login
        },
        {
            path: '/login',
            name: 'login',
            component: Login
        },
        {
            path: '/display',
            name: 'display',
            component: Display
        }
    ]
})